import React from "react"
import { Link, graphql } from "gatsby"
import Image from "../components/image"

import Layout from "../components/layout"
import SEO from "../components/seo"

import "@suziwen/gitalk/dist/gitalk.css"
import Gitalk from "gatsby-plugin-gitalk"

import DateStr2Date from "../components/date"

const BlogPostTemplate = ({ data, location }) => {
  const post = data.markdownRemark
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const { previous, next } = data
  let gitalkConfig = {
    id: post.fields.slug,
    title: post.frontmatter.title,
  }
  let date = null
  if (post.frontmatter.category === "diary") {
    date = post.frontmatter.created
  } else {
    date = post.frontmatter.updated
  }
  return (
    <Layout location={location} title={siteTitle}>
      <SEO
        title={post.frontmatter.title}
        description={post.frontmatter.description || post.excerpt}
      />
      <article
        className="blog-post"
        itemScope
        itemType="http://schema.org/Article"
      >
        <header>
          <small>
            <div className={`category ${post.frontmatter.category}`}>
              <Link to={`/category/${post.frontmatter.category}/`}>
                {post.frontmatter.category}
              </Link>
            </div>
          </small>
          <h1 itemProp="headline">{post.frontmatter.title}</h1>
          <p>
            <DateStr2Date dateStr={date} />
          </p>
          <div className="tag-list">
            {post.frontmatter.tag?.map(tag => {
              return (
                <small>
                  <div className="tag">
                    <Link to={`/tag/${tag}/`}>{tag}</Link>
                  </div>
                </small>
              )
            })}
          </div>
          <small className="profile-mini-list">
            {post.frontmatter.author?.map(name => {
              return (
                <Link to={`/author/${name}/`} className="profile-mini">
                  <figure>
                    <Image
                      filename={"profile-pic-" + name + ".jpg"}
                      style={{
                        borderRadius: `50%`,
                      }}
                      fixed={true}
                    />
                  </figure>
                  <div>{name}</div>
                </Link>
              )
            })}
          </small>
        </header>
        <hr />
        <section
          dangerouslySetInnerHTML={{ __html: post.html }}
          itemProp="articleBody"
        />
      </article>
      <nav className="blog-post-nav">
        <ul
          style={{
            display: `flex`,
            flexWrap: `wrap`,
            justifyContent: `space-between`,
            listStyle: `none`,
            padding: 0,
          }}
        >
          <li>
            {previous && (
              <Link to={previous.fields.slug} rel="prev">
                 {previous.frontmatter.title}
              </Link>
            )}
          </li>
          <li>
            {next && (
              <Link to={next.fields.slug} rel="next">
                {next.frontmatter.title} 
              </Link>
            )}
          </li>
        </ul>
      </nav>
      <Gitalk options={gitalkConfig} />
    </Layout>
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
        author {
          name
          summary
          social {
            github
          }
        }
      }
    }
    markdownRemark(id: { eq: $id }) {
      id
      excerpt(pruneLength: 160)
      html
      fields {
        slug
      }
      frontmatter {
        title
        created
        updated
        description
        tag
        author
        category
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`
